import React from 'react'
import Theme from '../Theme'

function openLink (path) {
  window.open(path, '_blank')
}

export default function Project (props) {
  return (
    <div style={styles.container} onClick={() => openLink(props.details.link)}>
      <img style={styles.image} src={props.details.image} />
      <div style={styles.body}>
        <span style={styles.title}>{props.details.title}</span>
        <p style={styles.details}>{props.details.description}</p>
        <ul style={styles.techList}>
          {props.details.tech.map((tech, index) => {
            return <span style={styles.tech}>{tech}</span>
          })}
        </ul>
      </div>
    </div>
  )
}

const styles = {
  container: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    margin: 16,
    padding: 16,
    width: 288,
    height: 'auto',
    borderRadius: Theme.RADIUS,
    background: Theme.PANE.LIGHT,
    boxShadow: '0px 1px 1px rgba(0, 0, 0, 0.20)',
    cursor: 'pointer'
  },
  image: {
    minWidth: 72,
    minHeight: 72,
    width: 72,
    height: 72,
    background: '#5c6a80',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    borderRadius: 6
  },
  body: {
    padding: 8,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center'
  },
  title: {
    color: Theme.TEXT.PRIMARY
  },
  details: {
    margin: 0,
    fontSize: 12,
    color: Theme.TEXT.SECONDARY
  },
  techList: {
    listStyle: 'none',
    display: 'flex',
    padding: 0,
    margin: 0,
    paddingTop: 8
  },
  tech: {
    paddingRight: 6,
    fontWeight: 'bold',
    fontSize: 12,
    color: '#7d8899'
  }
}



